import React from 'react'
import SearchBar from './SearchBar'
import youtube from '../apis/youtube'
import VideoList from './VideoList'
import VideoDetail from './VideoDetail'

const KEY = 'AIzaSyCHmnVC6Pv31AfeLJze9enlRcQxfOlWFoI'

class App extends React.Component  {

    state = { 
        videos: [],
        selectedVideo: null 
    }

    componentDidMount() {
        this.onTermSubmit('TEDtalks')
    }

    onTermSubmit = async (term) => {
        console.log(term)
        const results = await youtube.get('/search', {
                params: {
                    q: term,
                    part: 'snippet',
                    maxResults: 5,
                    type: 'video',
                    key: KEY
                }
            }
        )
        //console.log(results.data.items)
        this.setState({ 
            videos: results.data.items,
            selectedVideo: results.data.items[0]
        })
    }

    onVideoSelect = (v) => {
        //console.log('selected video - ' + v.snippet.title)
        this.setState({ selectedVideo: v })
    }

    render() {
        return (
            <div className="ui container">
                <div className="ui segment">
                    <SearchBar onFormSubmit={this.onTermSubmit} />
                </div> 
                <div className="ui segment grid">
                    <div className="ui row">
                        <div className="eleven wide column">
                            <VideoDetail video={this.state.selectedVideo} />
                        </div>
                        <div className="five wide column">
                            <VideoList videos={this.state.videos} onVideoSelect={this.onVideoSelect} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default App
